// Updated App.jsx with improved drag + shadow + visual drop feedback
import React, { useRef, useEffect, useState } from "react";
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import Hammer from "hammerjs";

export default function App() {
  const containerRef = useRef();
  const modelRef = useRef();
  const rendererRef = useRef();
  const cameraRef = useRef();
  const sceneRef = useRef();

  const [cameraStream, setCameraStream] = useState(null);
  const [modelPlaced, setModelPlaced] = useState(false);
  const scale = useRef(1);
  const rotation = useRef(0);
  const offset = useRef({ x: 0, y: 0 });

  useEffect(() => {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(
      70,
      window.innerWidth / window.innerHeight,
      0.01,
      100
    );
    camera.position.z = 2;
    sceneRef.current = scene;
    cameraRef.current = camera;

    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.shadowMap.enabled = true;
    containerRef.current.appendChild(renderer.domElement);
    rendererRef.current = renderer;

    scene.add(new THREE.AmbientLight(0xffffff, 0.6));
    const dirLight = new THREE.DirectionalLight(0xffffff, 1);
    dirLight.position.set(2, 5, 5);
    dirLight.castShadow = true;
    scene.add(dirLight);

    // Fake ground plane to catch shadows
    const shadowPlane = new THREE.Mesh(
      new THREE.PlaneGeometry(5, 5),
      new THREE.ShadowMaterial({ opacity: 0.3 })
    );
    shadowPlane.rotation.x = -Math.PI / 2;
    shadowPlane.position.y = -0.01;
    shadowPlane.receiveShadow = true;
    scene.add(shadowPlane);

    const loader = new GLTFLoader();
    loader.load(
      process.env.PUBLIC_URL + "/chair.glb",
      (gltf) => {
        const model = gltf.scene;
        model.traverse((child) => {
          if (child.isMesh) {
            child.castShadow = true;
            child.receiveShadow = true;
          }
        });
        model.scale.set(0.5, 0.5, 0.5);
        model.visible = false;
        scene.add(model);
        modelRef.current = model;
      },
      undefined,
      (error) => console.error("Failed to load GLB:", error)
    );

    const animate = () => {
      requestAnimationFrame(animate);
      renderer.render(scene, camera);
    };
    animate();

    const hammer = new Hammer(containerRef.current);
    hammer.get("pinch").set({ enable: true });
    hammer.get("rotate").set({ enable: true });
    hammer.get("pan").set({ direction: Hammer.DIRECTION_ALL });

    let lastPan = { x: 0, y: 0 };
    hammer.on("panstart", () => {
      lastPan = { x: 0, y: 0 };
    });

    hammer.on("panmove", (e) => {
      if (!modelRef.current || !modelPlaced) return;
      const dx = (e.deltaX - lastPan.x) / window.innerWidth * 2;
      const dy = (e.deltaY - lastPan.y) / window.innerHeight * 2;
      modelRef.current.position.x += dx;
      modelRef.current.position.z -= dy;
      lastPan = { x: e.deltaX, y: e.deltaY };
    });

    hammer.on("pinch", (e) => {
      if (!modelRef.current || !modelPlaced) return;
      scale.current = e.scale;
      modelRef.current.scale.setScalar(scale.current * 0.5);
    });

    hammer.on("rotate", (e) => {
      if (!modelRef.current || !modelPlaced) return;
      rotation.current = e.rotation;
      modelRef.current.rotation.y = rotation.current * (Math.PI / 180);
    });

    return () => hammer.destroy();
  }, []);

  useEffect(() => {
    const onClick = (e) => {
      if (!modelRef.current || modelPlaced) return;

      const bounds = containerRef.current.getBoundingClientRect();
      const x = ((e.clientX - bounds.left) / bounds.width) * 2 - 1;
      const y = -((e.clientY - bounds.top) / bounds.height) * 2 + 1;

      const raycaster = new THREE.Raycaster();
      raycaster.setFromCamera({ x, y }, cameraRef.current);

      const plane = new THREE.Plane(new THREE.Vector3(0, 1, 0), 0);
      const point = new THREE.Vector3();
      raycaster.ray.intersectPlane(plane, point);

      point.y = 0; // Place on ground
      modelRef.current.position.copy(point);
      modelRef.current.visible = true;
      setModelPlaced(true);
    };

    window.addEventListener("click", onClick);
    return () => window.removeEventListener("click", onClick);
  }, [modelPlaced]);

  useEffect(() => {
    async function startCamera() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: { exact: "environment" } },
        });
        setCameraStream(stream);
      } catch (err) {
        console.warn("Back camera not found, trying default camera...");
        const stream = await navigator.mediaDevices.getUserMedia({ video: true });
        setCameraStream(stream);
      }
    }
    startCamera();
  }, []);

  return (
    <>
      {cameraStream && (
        <video
          autoPlay
          playsInline
          muted
          ref={(video) => video && (video.srcObject = cameraStream)}
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            objectFit: "cover",
            zIndex: -1,
          }}
        />
      )}

      <div
        ref={containerRef}
        style={{
          width: "100vw",
          height: "100vh",
          position: "fixed",
          top: 0,
          left: 0,
          zIndex: 1,
        }}
      />

      <div
        style={{
          position: "absolute",
          bottom: "20px",
          width: "100%",
          textAlign: "center",
          color: "white",
          fontSize: "16px",
          textShadow: "0 0 5px black",
        }}
      >
        {modelPlaced
          ? "Drag, pinch, or rotate the object"
          : "Tap on screen to place the object"}
      </div>
    </>
  );
}
